const initialState = {
    allCountriesData: [],
    allCountriesNames: []
}

export default (state = initialState, action) => {
    switch (action.type) {
        case 'FETCH_ALL_COUNTRIES_DATA':
            return { ...state, allCountriesData: action.payload }
        case 'FETCH_ALL_COUNTRIES':
            return { ...state, allCountriesNames: action.payload }
        default:
            return state;
    }
}