import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { postNewTrip } from '../actions';
import { useHistory } from "react-router-dom";
// import Error from './Error';
// import { Dropdown } from 'react-bootstrap';

import Button from '@material-ui/core/Button';
// import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import '../styles/new-trip.css';


import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import { FormControl } from '@mui/material';
import Select from '@mui/material/Select';



const useStyles = makeStyles((theme) => ({
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        width: 200,
    },
    div: {
        display: 'block',
        padding: '6px 16px',
    }
}));

const NewTrip = ({ setAddNewTrip, setIsExist }) => {
    let history = useHistory();
    const classes = useStyles();
    const countryList = useSelector(state => state.allTrips.allTrips);
    const allCountriesNames = useSelector(state => state.allCountries.allCountriesNames);
    // const [errorFlag, setErrorFlag] = useState(false);
    const [countryName, setCountryName] = useState('');

    const createNewTrip = (e) => {

        e.preventDefault()
        //     if (!/^[a-zA-Z]+$/.test(countryName)) {
        //         setErrorFlag(true)
        //         setTimeout(() => {
        //             setErrorFlag(false)
        //         }, 4000);
        //     } else {
        if (!countryList.map((obj) => obj['country']).includes(countryName)) {
            postNewTrip(countryName);
            history.push(`/${countryName}`)
        } else {
            console.log("country already exist");
            setAddNewTrip(false)
            setIsExist(true)

            setTimeout(function () {
                setIsExist(false)
                history.push(`/${countryName}`)
            }, 3000)

            setTimeout(() => {
                console.log("cancel");
            }, 0)
        }
    }




    const handleChange = (e) => {
        setCountryName(e.target.value);
    };

    const getCountries = () => (
        allCountriesNames.map((countryName) => {
            return <MenuItem
                key={countryName}
                className={classes.div}
                value={countryName}>
                {countryName}
            </MenuItem>
        })

    )


    return (
        <div className="new-trip__container" >
            <div className="close-from-outside" onClick={() => setAddNewTrip(false)} ></div>
            <div className="new-trip__form-container">
                <i className="window close icon" onClick={() => setAddNewTrip(false)}></i>
                <h1 className="new-trip__form-header" >Start to Plan!</h1>
                <form className="new-trip__form" onSubmit={(e) => createNewTrip(e)}>
                    <table>
                        <tbody>
                            <tr>
                                <th>
                                    <h1 className="new-trip__form-country" htmlFor="country">Country: </h1>
                                </th>
                                <th className="th">
                                    {/* <TextField
                                        size="small"
                                        id="outlined-basic"
                                        label="Country"
                                        variant="outlined"
                                        value={countryName}
                                        onChange={(e) => setCountryName(e.target.value)}
                                    /> */}

                                    <FormControl sx={{ m: 1, minWidth: 120 }}>
                                        <InputLabel id="demo-simple-select-helper-label">Country</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-helper-label"
                                            id="demo-simple-select-helper"
                                            value={countryName}
                                            label="Country Name"
                                            onChange={handleChange}
                                        >
                                            {getCountries()}
                                        </Select>
                                    </FormControl>
                                </th>
                            </tr>
                            {/* <tr>
                                <th></th>
                                <div className="input_note">
                                    *Please insert only letters
                                </div>
                            </tr> */}
                        </tbody>
                    </table>
                    <br />
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                    >
                        Send
                    </Button>
                </form>
                {/* {errorFlag ? <Error input={'invalidString'} /> : null} */}
            </div>
        </div >
    )
}

export default NewTrip;
