import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { getAllPlans } from '../actions';
import { useParams } from 'react-router-dom';
import NewPlan from './NewPlan';
import Plan from './Plan';
import '../styles/trip-details.css';


const TripDetails = () => {
    const dispatch = useDispatch();
    const { countryName } = useParams();
    const tripsDetails = useSelector(state => state.allTrips.tripsDetails);
    const [addPlan, setAddPlan] = useState(false);
    const allCountriesData = useSelector(state => state.allCountries.allCountriesData);
    const [countryDetails, setCountryDetails] = useState([])


    useEffect(() => {
        dispatch(getAllPlans(countryName));
    }, [addPlan])

    useEffect(() => {
        const getCountryDetails = () => {
            setCountryDetails(allCountriesData.filter((country) => {
                return country['name'].common === countryName
            }))
        }
        getCountryDetails()
    }, [])


    return (
        <div className="trip-details__container">
            {countryDetails.length !== 0 && <div className="trip-details__header">
                <h1 className="trip-details__country">{countryName.toUpperCase()}</h1>
                <img
                    src={countryDetails[0]['flags'][0]}
                    alt={`flag of ${countryName}`}
                    className="country-flag"
                >
                </img>
                <br />
                {countryDetails[0]['capital'] && <span className="country-capital"><h2>Capital:</h2> <p>{countryDetails[0]['capital']}</p></span>}
            </div>}

            <div>
            </div>
            <button
                className="add-trip__icon"
                onClick={() => setAddPlan(!addPlan)}
            >
                <h2 className="all-trips__second">Add Plan {addPlan ? <i className="x icon"></i> : <i className='plus icon'></i>} </h2>
            </button>

            {addPlan ? <NewPlan setAddPlan={setAddPlan} /> : null}

            {tripsDetails.length !== 0 ? tripsDetails.map((currentPlan) => {
                let key = Object.keys(currentPlan)[0];
                let plan = Object.values(currentPlan)[0]
                return (
                    <Plan key={Math.floor(Math.random() * 1000)} id={key} planKey={currentPlan['key']} plan={plan} countryName={countryName} />
                )
            }) : null}
        </div >
    )
}

export default TripDetails;
